@use "../global/variables.scss";
@use "../global/mixins.scss";

.feature {
  @media (min-width: variables.$tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(256px, auto);
  }
}

.feature__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 24px 18px;
  background: variables.$grey-03;
  border-bottom: 1px solid variables.$grey-04;

  @media (min-width: variables.$tablet-width) {
    border-bottom: none;
  }

  &--accent {
    padding: 0;
    border: none;
    background: variables.$blue-01;

    @media (min-width: variables.$tablet-width) {
      grid-column: 1 / 4;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &:last-child {
    padding-bottom: 28px;
  }
}

.feature__item-wrapper {
  color: variables.$white;

  &--accent-first {
    padding: 54px 18px 40px;

    @media (min-width: variables.$tablet-width) {
      padding: 27px 0;
    }
  }

  &--accent-second {
    padding: 37px 18px;

    @media (min-width: variables.$tablet-width) {
      order: 2;
      padding: 27px 0;
    }
  }
}

.feature__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
}

.feature__subject {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-top: 11px;
}

.feature__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  margin: 0;
  margin-top: 17px;
}

.feature__img-wrapper {
  width: 100%;
  background: variables.$grey-04;

  @media (min-width: variables.$tablet-width) {
    &--right-img {
      grid-column: 2 / 4;
    }

    &--left-img {
      grid-column: 3 / 1;
    }
  }
}

.feature__img {
  @include mixins.img;
}

.feature__list {
  @include mixins.list-reset;

  @media (min-width: variables.$tablet-width) {
    grid-column: 1 / 4;
  }
}
