@use "../global/variables.scss";

.main-header__container {
  position: relative;
}

.main-header__wrapper {
  height: 56px;
  background: variables.$white;
}

.main-header__inner-wrapper {
  position: fixed;
  width: 100%;
  height: 56px;
  display: flex;
  align-items: center;
  background: variables.$white;
}

.main-header__logo-link {
  position: fixed;
  top: 0;
  right: 50%;
  transform: translateX(50%);
}

.main-header__toggle {
  position: fixed;
  top: 17px;
  right: 20px;
  z-index: 99;
  width: 23px;
  height: 23px;

  @media (min-width: variables.$tablet-width) {
    display: none;
  }
}
